.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top:0;
    z-index: 100;

}
.header__logo{
    width: 100px;
    margin-top: 18px;
    margin-right:20px;
    margin-left:20px;
}
.header__search{
    display: flex;
    flex: 1;
}
.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header__searchIcon{
   padding: 5px;
   height: 22px !important;
   background-color: #cd9042;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__link{
    color: white;
    text-decoration: none;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    margin-left: 10px;
}
.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header__optionBasketIcon{
    color: white;
}
.header__optionBasket{
    display: flex;
}
.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.header__github{
    display: flex;
    border:1px solid white;
    margin-right: 10px;
    margin-left: 10px;
}
.header__github a{
    text-decoration: none;
    color: white;
    align-items: center;
    display: flex;
    padding: 5px 10px;
}